@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Kanit', sans-serif;
}

body {
  background-image: linear-gradient(315deg, #e7e7e7, #f5f7f7,100%);
}

.game-content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  transform: scale(0.9);
}
.hide {
  display: none;
}
.btn {
  width: 27rem;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.click-image{
  cursor: pointer;
}
.card {
  width: 100vh;
}